  supprimer(pluviometrie){
    let e: any = {};
    let alert = this.alertCtl.create({
      title: 'Suppression pluviometrie',
      message: 'Etes vous sûr de vouloir supprimer cette pluviometrie ?',
      inputs: [
        {
          type: 'checkbox',
          label: 'Supprimer définitivement!',
          value: 'oui',
          checked: false
          }
      ],
      buttons:[
        {
          text: 'Non',
          handler: () => console.log('suppression annulée')
  
        },
        {
          text: 'Oui',
          handler: (data) => {
            if(data.toString() === 'oui'){
              this.servicePouchdb.deleteReturn(pluviometrie).then((res) => {
                //let e: any = {};
                //e.doc = essai;
                this.pluviometries.forEach((es, i) => {
                  if(es.doc._id === pluviometrie._id){
                    this.pluviometries.splice(i, 1);
                  }
                  
                });
    
                this.action = 'liste';
                //this.navCtrl.pop();
              }, err => {
                console.log(err)
              }) ;
            }else{
              this.servicePouchdb.deleteDocReturn(pluviometrie).then((res) => {
                //let e: any = {};
                //e.doc = essai;
                this.pluviometries.forEach((es, i) => {
                  if(es.doc._id === pluviometrie._id){
                    this.pluviometries.splice(i, 1);
                  }
                  
                });
    
                this.action = 'liste';
                //this.navCtrl.pop();
              }, err => {
                console.log(err)
              }) ;
            }
            
          }
        }
      ]
    });
  
    alert.present();
  }
